.modal {
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: aliceblue;
    height: 90%;
    max-width: 150vh;
    transform: scale(0.5);
    transition: 0.4s all;
}

.modal__content.active {
    transform: scale(1);
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.x {
    font-size: 26px;
    margin-left: 142vh;
    cursor: pointer;
    padding: 2px;
}